.SideDrawer {
  position: fixed;
  width: 280px;
  height: 100%;
  max-width: 70%;
  left: 0;
  top: 0;
  z-index: 200;
  background-color: #ffffffde;
  padding: 32px 16px;
  box-sizing: border-box;
  transition: transform 0.3s ease-out;
  @media (min-width: 500px) {
    display: none;
  }
  &.Open {
    transform: translateX(0)
  }

  &.Close {
    transform: translateX(-100%);
  }
  .logo {
    height: 11%;
    margin-bottom: 32px;
    background-color: transparent;
  }
}
