.Input {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
  }
  .InputElement {
    outline: none;
    border: 1px solid #ccc;
    background-color: #white;
    font: inherit;
    padding: 6px 10px;
    display:  block;
    width: 100%;
    box-sizing: border-box;
    &:focus{
      outline: none;
      background-color: #ccc;
    }
  }
  .Invalid{
    border-color: red;
    background-color: pink;
  }
}
